<template>
  <div :class="{'interchange-container-pc': isPc, 'interchange-container-mb': !isPc}">
    <!--<PublicHeader v-if="isPc"></PublicHeader>-->
    <div class="success-content">
      <div :class="{'steps-list': !isLogged, 'steps-list-logged': isLogged}">
        <div class="steps-icon">
          <div class="pay-radio hook">
            <i :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/hook-white.svg)'}"></i>
          </div>
          <div class="pay-radio hook" v-if="!isLogged">
            <i :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/hook-white.svg)'}"></i>
          </div>
          <div class="pay-radio number">
            <span>{{isLogged ? 2 : 3}}</span>
          </div>
        </div>
        <div class="steps-line"></div>
        <div class="steps-icon steps-tips">
          <span>{{$t('Choose Plan')}}</span>
          <span v-if="!isLogged">{{$t('Review Order')}}</span>
          <span>{{$t('Verify & Pay')}}</span>
        </div>
      </div>
      <div class="pay-success">
        <div class="card-wrap">
          <!--<i class="success-icon"></i>-->
          <div class="success-header-line"></div>
          <p class="success-title">支付訂單</p>
          <p class="success-description">付款後，即刻交付服務</p>
          <div class="card-detail">
            <div class="pay-sync" id="pay-sync">
              <a-spin v-if="!paySuccess && !payFailed">
                <a-icon slot="indicator" type="loading" style="font-size: 24px; color: #138CFF;" spin/>
              </a-spin>
              <a-icon v-if="paySuccess && !payFailed" type="check-circle" theme="twoTone" twoToneColor="#44B496"/>
              <a-icon v-if="payFailed" type="close-circle" theme="twoTone" twoToneColor="#F2585E"/>
              <span class="sync-result">{{payStatus}}</span>
            </div>
            <div class="header">
              <div class="btn-container" id="btn-container">
                <a-button class="submit-btn" type="primary" id="submit-btn"
                          @click="payNow()" :disabled="!gotTheDetail">
                  {{btnTips}}
                </a-button>
                <a-button class="submit-btn2" type="primary" id="submit-btn2" ghost
                          @click="userPaid()">
                  我已支付
                </a-button>
              </div>
              <p v-if="!payFailed" class="header-tips-footer">{{footerText}}</p>
              <p v-if="payFailed" class="header-tips-footer">支付遇到問題，<a @click="openZendesk()">聯繫客服</a></p>
            </div>
            <div v-if="!gotTheDetail" class="package-list-loading">
              <a-spin>
                <a-icon slot="indicator" type="loading" style="font-size: 30px" spin />
              </a-spin>
            </div>
            <div v-if="gotTheDetail" class="package-list">
              <p class="package-title">{{$t('Order Summary')}}</p>
              <div class="list-item">
                <p>訂單號</p>
                <p>{{invoiceInfo.invoiceid}}</p>
              </div>
              <div class="list-item">
                <p>套餐</p>
                <p>{{invoiceInfo.description}}</p>
              </div>
              <div class="list-item">
                <p>總額</p>
                <p>{{invoiceInfo.balance}} USD</p>
              </div>
              <div class="list-item">
                <p>付款方式</p>
                <p>{{invoiceInfo.paymentMethod}}</p>
              </div>
              <div class="list-item">
                <p>訂單日期</p>
                <p>{{invoiceInfo.duedate}}</p>
              </div>
              <div class="list-item">
                <p>訂單狀態</p>
                <p>{{invoiceInfo.status}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div style="display: none; visibility: hidden">
      <!-- START ExoClick Goal Tag | Sign Up -->
      <script type="application/javascript" src="https://a.exoclick.com/tag_gen.js" data-goal="5f3934ee3fb895723593751c8889a35a"></script>
      <!-- END ExoClick Goal Tag | Sign Up -->
    </div>
  </div>
</template>

<script>
  import Loading from '@/components/loading.vue';
  import i18n from '../i18n';

  export default {
    name: 'interchange',
    components: {
      Loading
    },
    data() {
      return {
        loading: false,
        resendTip: false,
        secondNum: 60,
        invoiceInfo: {
          invoiceid: '',
          description: '',
          balance: '',
          paymentMethod: '',
          duedate: '',
          status: '',
          paidDate: '',
          serviceId: '',
          datepaid: ''
        },
        isAndroid: false,
        isIos: false,
        isPc: true,
        isLogged: false,
        isLogin: false,
        btnTips: '立即支付',
        payStatus: '付款狀態同步中',
        paySuccess: false,
        payFailed: false,
        invoiceId: 0,
        intervalId: '',
        footerText: '請勿關閉本頁面，否則支付可能失敗。',
        payMethodList: [
          {
            id: 0,
            name: '支付寶',
            logo: ['alipay'],
            type: 'payssionalipaycn'
          },
          {
            id: 1,
            name: '信用卡',
            logo: ['visa', 'master', 'unionpay', 'amex'],
            type: 'stripe3dsecure'
          },
          {
            id: 2,
            name: 'PayPal',
            logo: ['paypal'],
            type: 'paypalcheckout'
          },
          {
            id: 3,
            name: '加密貨幣',
            logo: ['btc', 'btc2', 'eth', 'eth2'],
            type: 'coinpayments'
          }
        ],
        isSentData: false,
        payUrl: '',
        isPaying: false,
        gotTheDetail: false,
        userMaxRetry: 1,
        retryCount: 0,
        isUserRetry: false
      };
    },
    created() {
      this.isPc = this.$isPc()
      const matched = window.location.search.match(/code=(.*)/);
      if (matched !== null && matched.length > 1) {
        // the url is url-encoded, so we need reverse it, or the token isn't correct
        const decoded = decodeURIComponent(matched[1]);
        const redirectUrl = encodeURIComponent(`${window.location.origin}/WHMCS`);
        // const redirectUrl = 'https://uat.jetstream.surf/WHMCS';

        // const gclid = document.cookie.match(/_gcl_aw=(.*);/);
        // if (gclid && gclid.length > 1) {
        //   window.open(`${process.env.VUE_APP_SERVER_URL}/api/v1/orders/quickbuy/${decoded}?redirectTo=${redirectUrl}&gclid=${gclid[1].match(/\..+\.(.*)/)}`, '_blank');
        // } else {
        //   window.open(`${process.env.VUE_APP_SERVER_URL}/api/v1/orders/quickbuy/${decoded}?redirectTo=${redirectUrl}`, '_blank');
        // }
        const that = this;
        this.$request.getVerifyQuickBuy(decoded, { redirectTo: redirectUrl }, function (data) {
          if (!!data && data.status === 200) {
            // persist token between reloads
            localStorage.setItem('token', data.data.token);
            localStorage.setItem('email', data.data.email);
            window.zE(function() {
              if (!!localStorage.getItem("email")) {
                 window.zE.identify({
                   name: localStorage.getItem("email")
                          .split('@')[0],
                  email: localStorage.getItem("email"),
                });
              }
              window.zE.setLocale('zh-TW');
            });
            that.invoiceId = data.data.invoiceId;
            that.payUrl = decodeURIComponent(data.data.redirect);
            if (that.payUrl.indexOf("WHMCS") > -1 && that.payUrl.indexOf("?") > -1) {
              that.payUrl = that.payUrl + "&language=chinese";
            }
            // that.payUrl = data.data.redirect
            that.getInvoiceDetail()
            // Send the event to GTM
            window.dataLayer.push({'event': 'signUpCompleted'})
          } else {
            that.$notification.open({
              message: '郵件驗證碼已失效，請重新獲取',
              onClick: () => {
              },
            });
          }
        });
      } else {
        //this.isLogin = !!localStorage.getItem('token');
        if (this.$route.params.invoiceId != 0) {
          this.isLogin = true;
          this.isLogged = true;
          this.invoiceId = this.$route.params.invoiceId;
          this.payUrl = decodeURIComponent(this.$route.params.redirect);
          if (this.payUrl.indexOf("WHMCS") > -1 && this.payUrl.indexOf("?") > -1) {
             this.payUrl = this.payUrl + "&language=chinese";
          }
          this.getInvoiceDetail();
        }
      }
    },
    mounted() {
      let exoclick = document.createElement("div");
      exoclick.setAttribute('style', 'display: none; visibility: hidden')
      exoclick.innerHTML = `<script type="application/javascript" src="https://a.exoclick.com/tag_gen.js" data-goal="5f3934ee3fb895723593751c8889a35a"><\/script>`
      document.body.appendChild(exoclick)
    },
    methods: {
      payNow() {
        if (this.invoiceId != 0) {
          const that = this;
          if (this.isPaying) {
            // If is paying, that mean user trying repay
            if (that.payUrl != null) {
                if (that.payUrl.indexOf("WHMCS") > -1) {
                  localStorage.setItem("src",that.payUrl);
                  window.open(`${window.location.origin}/${that.$i18n.locale}/order/viewInvoice`, '_blank');
                } else {
                  window.open(that.payUrl, '_blank')
                }
              if (that.isUserRetry) {
                // If payFailed before, need reset all flag for repay
                that.payStatus = '付款狀態同步中'
                that.payFailed = false
                that.isUserRetry = false
                that.retryCount = 0
                that.intervalId = setInterval(function () {
                  that.getInvoiceDetail();
                }, 4000);
              }
            }
          } else if (this.paySuccess) {
            window.open(`${window.location.origin}/my/service`, '_blank')
            // } else if (this.intervalId.length > 0) {
          } else if (this.intervalId.length === 0) {
            this.isPaying = true
            TweenMax.fromTo(document.getElementById('btn-container'),
              0.3,
              { marginTop: '0' },
              {
                css: { marginTop: that.isPc? '65px': '1.253rem' },
                onStart: function () {
                  TweenMax.fromTo(document.getElementById('submit-btn'),
                    0.4,
                    {
                      width: that.isPc? '386px': '7.867rem',
                      left: '0'
                    },
                    {
                      width: that.isPc? '176px': '3.68rem',
                      left: that.isPc? '210px': '4.187rem'
                    }
                  );
                  TweenMax.fromTo(document.getElementById('submit-btn2'),
                    0.4,
                    { width: that.isPc? '386px': '7.867rem' },
                    {
                      css: { width: that.isPc? '176px': '3.68rem' },
                      onComplete: function () {
                        that.btnTips = '重新支付';
                        if (that.payUrl != null) {
                            if (that.payUrl.indexOf("WHMCS") > -1) {
                              localStorage.setItem("src",that.payUrl);
                              window.open(`${window.location.origin}/${that.$i18n.locale}/order/viewInvoice`, '_blank');
                            } else {
                              window.open(that.payUrl, '_blank')
                            }
                        }
                      }
                    }
                  );
                }
              }
            );
            TweenMax.fromTo(document.getElementById('pay-sync'),
              0.4,
              { opacity: 0 },
              { opacity: 1 }
            );
            this.intervalId = setInterval(function () {
              that.getInvoiceDetail();
            }, 4000);
          } else {
            this.$notification.open({
              message: '郵件驗證碼已失效，請重新獲取',
              onClick: () => {
              },
            });
          }
        } else {
          this.$notification.open({
            message: '郵件驗證碼已失效，請重新獲取',
            onClick: () => {
            },
          });
        }


      },
      userPaid() {
        if (this.payFailed) {
          // If payFailed before, need to re-pay
          this.$notification.open({
            message: '支付失敗，請重新支付',
            onClick: () => {
            },
          });
        } else {
          this.payStatus = "檢測支付狀態中"
          this.getInvoiceDetail()
          this.isUserRetry = true
        }
      },
      getInvoiceDetail() {
        // Get the invoice detail
        const that = this;
        this.$request.getUsersInvoiceDetail(this.invoiceId, function (data) {

          if (data && data.status === 200) {
            that.gotTheDetail = true
            // get the invoice info
            that.invoiceInfo.invoiceid = data.data.invoiceid;
            let buf = data.data.items.item[0].description
            that.invoiceInfo.description = buf.substring(0, buf.toLowerCase().indexOf('unlimited') + 9);
            that.invoiceInfo.balance = data.data.total;
            // this.invoiceInfo.paymentMethod = data.data.paymentmethod;
            that.invoiceInfo.duedate = data.data.duedate;
            that.invoiceInfo.status = i18n.t(data.data.status === 'Paid' ? 'Paid' : 'Unpaid');
            that.invoiceInfo.paidDate = data.data.paidDate;
            that.invoiceInfo.serviceId = data.data.items.item[0].relid;
            that.invoiceInfo.datepaid = data.data.datepaid;
            // get the payment method name
            const list = that.payMethodList.find(value => {
              return value.type === data.data.paymentmethod;
            });
            if (list && list.length > 0) {
              that.invoiceInfo.paymentMethod = list[0].name;
            } else {
              that.invoiceInfo.paymentMethod = data.data.paymentmethod;
            }
            if (data.data.status === 'Paid') {
              // If user has already paid, then need to get the service for getting the pid and billingcycle
              that.$http.get(`${process.env.VUE_APP_SERVER_URL}/api/v1/services`)
                .then(data => {
                  if (data && data.status === 200) {
                    data.data.products.forEach((item, index) => {
                      if (item.id === that.invoiceInfo.serviceId) {
                        // Stop the timer loop
                        window.clearInterval(that.intervalId);
                        // Update UI
                        that.paySuccessAnimation();

                        // Send the data GTM
                        if (!that.isSentData) {
                          that.isSentData = true;
                          // web GTM
                          window.dataLayer.push({
                            'event': 'purchase',
                            'ecommerce': {
                              'purchase': {
                                'actionField': {
                                  'id': that.invoiceInfo.invoiceid,                         // order id
                                  'revenue': that.invoiceInfo.balance,                     // 收入
                                  'coupon': ''                 // 优惠码，没有则为空
                                },
                                'products': [{                            // List of productFieldObjects.
                                  'name': item.name,     // 产品名
                                  'id': item.productId,                          // pid
                                  'price': that.invoiceInfo.balance,
                                  'quantity': 1,
                                  'coupon': ''                            // 优惠码，没有则为空
                                }]
                              }
                            }
                          });

                        } else if (that.isAndroid) {
                          // back to the app (just for outside browser)
                          setTimeout(function () {
                            let a = document.createElement('a');
                            a.target = '_blank';
                            a.href = `whaleblue://test.com?data=${that.invoiceInfo.toString()}`;
                            a.click();
                          }, 2000);
                        }
                      }
                    });
                  }
                });

            } else if (that.isUserRetry) { // If this is user trigger to query
              // Set the max retry (can not just try once)
              if (that.retryCount > that.userMaxRetry) {
                // Pay Failed, need to re-pay
                window.clearInterval(that.intervalId)
                that.payFailed = true
                that.payStatus = '支付失敗，請重新支付'
              }
              that.retryCount ++
            }

            if (that.isLogin) {
               that.isLogin = false;
               that.payNow();
            }
          }
          // else {
          //   window.clearInterval(this.intervalId);
          //   // update UI
          //   this.loading = false;
          //   this.statusTip = '訂單未找到！data && data.status === 200';
          // }
        });

        // .catch(reason => {
        //   window.clearInterval(this.loopTimer);
        //   // update UI
        //   this.loading = false;
        //   this.statusTip = '訂單未找到！catch';
        // });
      },
      paySuccessAnimation() {
        const that = this;
        TweenMax.fromTo(document.getElementById('submit-btn'),
          0.5,
          {
            width: that.isPc? '176px': '3.68rem',
            left: that.isPc? '210px': '4.187rem'
          },
          {
            width: that.isPc? '386px': '7.867rem',
            left: '0'
          },
        );
        TweenMax.fromTo(document.getElementById('submit-btn2'),
          0.5,
          { width: that.isPc? '176px': '3.68rem' },
          {
            css: { width: that.isPc? '386px': '7.867rem' },
            onComplete: function () {
              that.isPaying = false
              that.paySuccess = true;
              that.btnTips = '查看服務';
              that.payStatus = '支付成功';
              that.footerText = '查看套餐信息與使用教程';
            }
          }
        );
      },
      openZendesk() {
        window.zE('webWidget', 'toggle');
      }
    }
  };
</script>

